<template lang="pug">
  
  .gps-wrapper
    .header
      .nav
        button(@click='isEdit = !isEdit').nav__option {{isEdit ? "Готово" : "Избранное"}}

      h1.header__title Навигатор

      .header-search
        svg.header-search__icon
          use(xlink:href='#Search')
        input(
          type='text' 
          placeholder='Поиск'
          v-model.trim='search').header-search__input
          
    GpsList(:search='search' :isEdit='isEdit')

</template>

<script> 

  import GpsList from '../components/GPS/GpsList'

  export default {
    components: { GpsList },
    data() {
      return {
        search: '',
        isEdit: false
      }
    },
    created() {
      this.$store.commit('REFRESH_SYSTEM_STYLE', true);
    }
  } 

</script>

<style lang="sass">

  @import '../assets/sass/phone/apps/gps/main'

</style>