<template lang="pug">
  
  .gps-list
    .swiper-container
      .swiper-wrapper
        .swiper-slide(
          v-for='(category, index) in categories' @click='selectedCategory = index'
        )
          p.gps-list__item(
            :class='{"gps-list__item_selected": index === selectedCategory}'
          ) {{category}}

    .gps-points
      p.phone__info(v-if='!filterPoints.length').
        {{search ? 'Нет совпадений' : 'Список пуст'}}

      .gps-points__item-wrapper(
        v-else v-for='(point, index) in filterPoints' @click='selectPoint(point)'
      )
        .gps-points__item
          p.gps-points__name {{point.name}}
          svg(
              v-if='isEdit' 
              :class='{"gps-points__favourite-icon_selected": point.favourite}'
              @click.stop='addToFavourite(point.name)').gps-points__favourite-icon
              use(xlink:href='#star')
          .gps-points__selected(v-if='point.selected && !isEdit')
            p.gps-points__distance {{pointDistance}} км.


            svg.gps-points__gps-icon
              use(xlink:href='#gps')

</template>

<script>

    import {mapState} from 'vuex'
    import Swiper from 'swiper'
    import 'swiper/swiper-bundle.css'

    export default {
      props: { search: String, isEdit: Boolean },
      data() {
        return {
          selectedCategory: 0
        }
      },
      computed: {
        ...mapState({
          categories: state => state.gps.categories,
          points: state => state.gps.points,
          pointDistance: state => state.gps.distanceToPoint
        }),
        filterPoints() {
          let currCategory = this.categories[this.selectedCategory];
          
          let filteredPoints = this.points.filter(item => 
            item.favourite === true && currCategory === 'Избранное' || item.category === currCategory
          );

          if (!this.search) 
            return filteredPoints

          else 
            return filteredPoints.filter(point => 
              point.name.toLowerCase().includes(this.search.toLowerCase())
            )
        },
      },
      methods: {
        selectPoint(point) {
          this.$store.commit('SELECT_POINT', this.points.indexOf(point))

          point.selected 
            ? this.$alert.show('Метка поставлена')
            : this.$alert.show('Метка убрана')

        },
        addToFavourite(name) {
          this.$store.commit('SET_FAVOURITE_POINT', name)
        }
      },
      mounted() {
        let swiper = new Swiper('.swiper-container', {
          slidesPerView: 'auto',
          spaceBetween: 21,
          freeMode: true,
          slidesOffsetBefore: 17,
          slidesOffsetAfter: 55
        })

        this.$nextTick(() => swiper.update())
      }
    }

</script>